.lvl-range {
  box-sizing: border-box;
  position: relative;
  width: 100%;
  padding: 2px 0 0 0;

  @media (min-width: $tablet-width) and (max-width: $desktop-width) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 9px 3px 0 5px;
  }
}

.lvl-range__output {
  display: flex;
  height: 35px;
  margin: 0 0 23px 0;
  background-color: #ffffff;
  border-radius: 4px;

  @media (min-width: $tablet-width) and (max-width: $desktop-width) {
    order: 2;
    width: 148px;
    height: 46px;
    margin: 0 0 0 0;
  }

  @media (min-width: $desktop-width) {
    width: 192px;
    height: 46px;
    margin: 8px 0 36px 3px;
  }
}

.lvl-range__value {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  padding: 4px 0 0 0;

  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 13px;
  text-align: center;
  letter-spacing: 0.07em;
  color: $button-text-color;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 20px;
    letter-spacing: 0.04em;
  }

  &--lower {
    border: 1px solid $input-border-color;
    border-right-width: 0.5px;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;

    &:hover {
      border-color: $lvl-border-hover-color;
    }

    &:focus-within {
      border-color: $site-bg-color-dark;
    }
  }

  &--upper {
    border: 1px solid $input-border-color;
    border-left-width: 0.5px;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;

    &:hover {
      border-color: $lvl-border-hover-color;
    }

    &:focus-within {
      border-color: $site-bg-color-dark;
    }

    &::before {
      content: "";
      display: block;
      position: absolute;
      top: 35%;
      left: -9px;
      width: 16px;
      height: 11px;
      background: linear-gradient(to bottom, #ffffff 4.5px, $input-border-color 4.5px, $input-border-color 6px, #ffffff 6px);
    }
  }
}

.lvl-range__input {
  display: block;
  width: 100%;
  height: 100%;
  padding-left: 15px;
  border: 0;
  outline: none;

  font-family: inherit;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 13px;
  text-align: center;
  letter-spacing: 0.07em;
  color: $button-text-color;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 20px;
    letter-spacing: 0.04em;
  }
}

.lvl-range__controls-container {
  @media (min-width: $tablet-width) and (max-width: $desktop-width) {
    width: 57%;
  }

  @media (min-width: $desktop-width) {
    margin: 0 0 37px 3px;
  }
}

.lvl-range__controls {
  box-sizing: border-box;
  position: relative;
  width: 100%;

  @media (min-width: $tablet-width) and (max-width: $desktop-width) {
    order: 1;
    margin: 0 0 0 0;
  }
}

.lvl-range__controls-scale {
  display: block;
  width: 100%;
  height: 2px;
  background: rgba($button-text-color, 0.2);

  @media (min-width: $tablet-width) {
    height: 3px;
    margin: 0 0 0 0;
  }
}

.lvl-range__controls-bar {
  height: 100%;
  margin-left: 31%;
  background: $button-text-color;

  @media (min-width: $tablet-width) and (max-width: $desktop-width) {
    margin: 0 0 0 40%;
  }
}

.lvl-range__toggle {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: -5px;

  &:hover {
    &::before {
      transform: scale(1.4);
    }
  }

  &:active {
    &::before {
      box-shadow: 0 3px 7px rgba(0, 0, 0, 0.24);
    }
  }

  &::before {
    content: "";
    display: block;
    width: 10px;
    height: 10px;
    background: $button-text-color;
    border-radius: 50%;
    cursor: pointer;

    @media (min-width: $tablet-width) {
      width: 13px;
      height: 13px;
    }
  }

  &--value-lower {
    left: 30.5%;

    @media (min-width: $tablet-width) and (max-width: $desktop-width) {
      left: 36.5%;
    }
  }

  &--value-upper {
    right: 0;
  }
}
